<template>
  <div id="Readersetting">
    <!-- <i class="closeCheaper" @click="close = !close"></i> -->
    <div class="list">
      <div class="cheaper-title">目录</div>
      <li @click="reverse" class="relist">
        <span v-if="rever">倒序</span>
        <span v-if="!rever">正序</span>
      </li>
      <li
        v-for="(item, index) in list.chapters"
        :key="index"
        :class="{lockcheaper:item.link==lock.readlink}"
        @click="readbook(item.link,list._id,item.order)"
      >{{item.title}}</li>
    </div>
  </div>
</template>
<script>
export default {
  name: "Readersetting",
  props: ["lock"],
  component: {},
  computed: {
    updateBookChapter: function() {
      return this.$store.state.updateBookChapter;
    }
  },
  data() {
    return {
      list: [],
      close: false,
      rever: true
    };
  },
  created() {
    if (sessionStorage.getItem(`${this.lock.orid}-chaptersList`)) {
      this.list = JSON.parse(
        sessionStorage.getItem(`${this.lock.orid}-chaptersList`)
      );
    }
  },
  methods: {
    closeList() {
      this.close = !this.close;
    },
    reverse() {
      this.rever = !this.rever;
      this.list.chapters.reverse();
    },
    readbook(link, cid, index) {
      window.localStorage.setItem(
        `${this.lock.orid}-schedule`,
        JSON.stringify(index)
      );
      this.$router.replace({
        name: "Reader",
        query: {
          readlink: link,
          id: cid,
          n: index,
          orid: this.lock.orid
        }
      });
    }
  }
};
</script>
<style lang="less">
#Readersetting {
  transition: all 0.6s;
  z-index: 100;
  top: 0;
  right: -200vw;
  position: fixed;
  padding-top: 0;
  &::after {
    content: "";
    display: block;
    clear: both;
  }

  .list {
    transition: all 0.5s;
    position: relative;
    width: 90vw;
    height: 100vh;
    right: 0;
    overflow: scroll;
    background: #fff;
    top: 0;
    .relist {
      text-align: right;
      padding: 0.5rem 2rem;
      span {
        color: #ff2525;
      }
    }
    .cheaper-title {
      color: #ff2525;
      font-weight: normal;
      font-size: 1.1rem;
      padding: 1rem 0;
      border-bottom: 1px solid #ff2525;
      span {
        font-size: 0.9rem;
        color: #000;
      }
    }
    li {
      padding: 1rem 0;
      text-align: left;
      padding-left: 1rem;
      border-bottom: 1px solid #d3d3d3;
    }
  }
  .hide {
    margin-right: 200vw;
  }
  .lockcheaper {
    color: #ff2525;
  }
}
</style>
