<template>
  <div id="Reader" :class="{moon:moon}">
    <Topnav class="Reader-topnav" :class="{blur: this.showList,shownav:this.show,moon:moon}"></Topnav>
    <div @click="hidelist">
      <div class="top-title" :class="{blur:showList,moon:moon}">{{content.title}}</div>
      <div class="txt-content" :class="{blur:showList,moon:moon}">
        <h3>{{content.title}}</h3>
        <div v-if="imgcontent">
          <div class="img-content" v-for="(item, index) in imgcontent" :key="index">
            <img :src="item" alt>
          </div>
        </div>
        <div class="err" v-if="content.isVip">抱歉，当前章节只供VIP浏览</div>
        <div class="err" v-if="erro">抱歉，网络出了点小差错</div>
        <p v-if="!content.isVip" :style="{fontSize: fontSize + 'rem' }">{{content.cpContent}}</p>
      </div>
    </div>
    <div class="Readerbottom" :class="{blur:showList,showbottom:this.show}">
      <el-button type="info" icon="el-icon-arrow-left" @click.native="prev"></el-button>
      <span class="loglist" @click="showList=!showList">目录</span>
      <el-button type="info" icon="el-icon-arrow-right" @click.native="next"></el-button>
      <span class="lightTogle">
        <i :class="{'el-icon-sunny':moon,'el-icon-moon':!moon}" @click="moonmode"></i>
      </span>

      <span class="fontbtn" @click="changefont(-0.1)">
        A
        <i class="el-icon-minus"></i>
      </span>
      <span class="fontbtn" @click="changefont(0.1)">
        A
        <i class="el-icon-plus"></i>
      </span>
    </div>
    <Readersetting :lock="linkparmes" :class="{Listactive:showList}"></Readersetting>
  </div>
</template>
<script>
import { Loading } from "element-ui";
import Readersetting from "../components/Readersetting.vue";
import Topnav from "../components/Topnav.vue";
export default {
  name: "Reader",
  inject: ["reload"],
  data() {
    return {
      linkparmes: [],
      content: [],
      imgcontent: [],
      loading: true,
      canscroll: false,
      showList: false,
      show: false,
      cheaptern: [],
      moon: false,
      erro: false,
      fontSize: 1
    };
  },
  components: {
    Readersetting,
    Topnav
  },
  methods: {
    changefont(x) {
      if (this.fontSize != 0.1) {
        this.fontSize = Number(this.fontSize) + Number(x);
      }
    },
    moonmode() {
      this.moon = !this.moon;
    },
    hidelist() {
      this.show = !this.show;
      if (this.showList) {
        this.showList = false;
      }
    },
    next() {
      window.localStorage.setItem(
        `${this.linkparmes.orid}-schedule`,
        JSON.stringify(this.linkparmes.n)
      );
      this.cheaptern = JSON.parse(
        sessionStorage.getItem(`${this.linkparmes.orid}-chaptersList`)
      ).chapters[Number(this.linkparmes.n) + Number(1)].link;
      this.$router.replace({
        name: "Reader",
        query: {
          readlink: this.cheaptern,
          id: this.linkparmes.id,
          n: Number(this.linkparmes.n) + Number(1),
          orid: this.linkparmes.orid
        }
      });
    },
    prev() {
      window.localStorage.setItem(
        `${this.linkparmes.orid}-schedule`,
        JSON.stringify(this.linkparmes.n)
      );
      this.cheaptern = JSON.parse(
        sessionStorage.getItem(`${this.linkparmes.orid}-chaptersList`)
      ).chapters[Number(this.linkparmes.n) - Number(1)].link;
      this.$router.replace({
        name: "Reader",
        query: {
          readlink: this.cheaptern,
          id: this.linkparmes.id,
          n: Number(this.linkparmes.n) - Number(1),
          orid: this.linkparmes.orid
        }
      });
    }
  },
  created() {
    let linkparme = this.$route.query;
    this.linkparmes = linkparme;
    let loadingInstance = Loading.service({ fullscreen: true });
    let readurl = `https://api.kele8.cn/agent?url=http://chapterup.zhuishushenqi.com/chapter/${
      this.linkparmes.readlink
    }`;
    axios
      .get(readurl)
      .then(res => {
        this.content = res.data.chapter;
        if (res.data.chapter.images) {
          this.imgcontent = res.data.chapter.images.split(",");
        }
        this.$nextTick(() => {
          loadingInstance.close();
        });
      })
      .catch(err => {
        console.error(err);
        this.erro = true;
      });
  },
  beforeRouteUpdate(to, from, next) {
    this.reload();
    next();
  }
};
</script>
<style lang="less">
.blur {
  filter: brightness(30%) blur(5px);
}
.light {
  color: #8d8d8d;
  background: #1a1a1a;
}
#Reader {
  transition: all 0.6s;
  width: 100%;
  background: url(../assets/bg.jpg) no-repeat center top,
    url(../assets/2.jpg) no-repeat center bottom,
    url(../assets/3.jpg) repeat-y center 119px;
  background-size: 100%;
  position: relative;
  img {
    display: block;
    max-width: 100%;
  }
  &.moon {
    .light;
  }
  .err {
    font-size: 1.5rem;
    font-weight: bold;
    margin-top: 50%;
  }
  .Listactive {
    right: 0;
  }
  .Reader-topnav {
    transition: all 0.3s;
    width: 100%;
    top: -3rem;
    position: fixed;
    color: #fff;
    background: rgba(0, 0, 0, 0.8);
  }
  .top-title {
    width: 100vw;
    font-size: 0.9rem;
    padding: 0.5rem;
    background: url(../assets/bg.jpg) no-repeat center top;
    position: fixed;
    text-align: left;
    &.moon {
      .light;
    }
  }
  .nextbtn {
    margin-bottom: 3rem;
  }
  .txt-content {
    min-height: 100vh;
    transition: all 0.6s;
    color: #000;
    width: 100%;
    padding-top: 1rem;
    padding-bottom: 2rem;
    &.moon {
      .light;
    }
    h3 {
      font-size: 1.5rem;
      margin: 2.25rem auto;
      text-size-adjust: 100%;
    }
    p {
      font-size: 1.1rem;
      line-height: 1.8;
      text-align: justify;
      width: 100%;
      margin: 0 auto;
      font-weight: 500;
      text-indent: 2rem;
      white-space: pre-wrap;
      word-wrap: break-word;
      text-size-adjust: 100%;
      font-weight: normal;
    }
  }
  .Readerbottom {
    transition: all 0.3s;
    width: 100%;
    background: rgba(0, 0, 0, 0.8);
    bottom: -3rem;
    position: fixed;
    line-height: 2.25rem;
    z-index: 10;
    font-size: 1.5rem;
    padding: 0.25rem;
    display: flex;
    .el-button {
      padding: 0.3rem 0.4rem;
    }
    .el-button--info {
      font-size: 1rem;
      background: rgba(0, 0, 0, 0.5);
    }
    .loglist {
      position: relative;
      font-size: 1.1rem;

      color: #fff;
    }
    .lightTogle {
      font-size: 1rem;
      position: relative;
      display: inline-block;
      margin-left: 1.5rem;
      color: #fff;
    }
    .fontbtn {
      font-size: 1rem;
      color: #fff;
    }
    span {
      flex: 1;
    }
  }
  .shownav {
    top: 0;
  }
  .showbottom {
    bottom: 0;
  }
}
</style>
